import React, { Component } from 'react';
import Head from '../components/Head';
import axios from 'axios';
import config from '../config';
class Index extends Component {
    constructor(props) {
        super(props);
        this.state = {
            arrDares: [],
            currentDare: '',
            loading: false,
            startTime: 0,
            currentTime: 0
        };
    }
    componentDidMount() {
        this.setState({ loading: true })
        const query = `https://graph.facebook.com/${config.post_id}/comments?fields=message,from&access_token=${config.access_token}`
        this.getAllComments(query)
    }
    getAllComments = (query) => {
        axios.get(query)
            .then(res => {
                const resData = res.data
                const { data, paging } = resData
                const dataFiltter = data.filter(item => (!item.message.includes('Gọi điện')))
                this.setState({ arrDares: this.state.arrDares.concat(dataFiltter) })
                if (paging && paging.next) {
                    this.getAllComments(paging.next)
                } else {
                    this.setState({ loading: false })
                }
            })

    }
    sleep = ms => new Promise(resolve => setTimeout(resolve, ms));


    generateDare = async () => {
        if (this.state.loading) return
        this.setState({ loading: true })
        await this.sleep(3000)
        const random = Math.floor(Math.random() * this.state.arrDares.length)
        this.setState({ currentDare: this.state.arrDares[random], loading: false })
    }
    render() {
        const { currentDare, loading, startTime, currentTime } = this.state
        const fromName = currentDare.from ? currentDare.from.name : ''
        const style = {
            container: {
                height: '100vh',
            },
            tren: {
                width: '100%',
                height: '70%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                backgroundColor: 'blue'
            },
            duoi: {
                width: '100%',
                height: '30%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
            }
        }
        return (
            <div className="">
                <Head></Head>
                <div style={style.container} className="" >
                    <h6 className="center-align">Develope by @xuanhuyjs</h6>
                    <div style={style.tren} className="teal">
                        {!loading ? (<div>
                            <div className="card blue-grey darken-1">
                                <div className="card-content white-text">
                                    <p>{currentDare.message || 'Welcome to #DAREHUB'}</p>
                                </div>
                                <div className="card-action right-align">
                                    <span>{fromName}</span>
                                </div>
                            </div>
                        </div>) : null}

                    </div>
                    {loading ? (<div className="progress" >
                        <div className="indeterminate"></div>
                    </div>) : null}
                    <div style={style.duoi} className="center-align blue-grey darken-1" onClick={this.generateDare} >
                        <a className="waves-effect waves-light btn-large">{loading ? 'Loading...' : 'generate dare'}</a>
                    </div>
                    <a id="scale-demo" href="#!" className="btn-floating btn-large scale-transition">
                        <i className="material-icons">add</i>
                    </a>
                </div>

            </div>
        );
    }
}

export default Index;
